// Bootstrap core variables and mixins
@import "bootstrap/variables.less";
@import "bootstrap/mixins.less";

// ID7 Brand configuration variables
@import "variables";
@import "mixins";

@icon-font-path: "../fonts/";
@body-bg: transparent;
@font-family-sans-serif: "Lato", "Helvetica Neue", Helvetica, Arial, sans-serif;
@headings-font-weight: 500;

@navbar-height: @id7-navigation-height;
@navbar-margin-bottom: 0;

@navbar-padding-horizontal: 0;
@navbar-border-radius: 0;

@input-color: #222222;

@font-size-h1: floor((@font-size-base * 2.25)); // ~31px
@font-size-h2: floor((@font-size-base * 2)); // ~28px

@border-radius-base: 0px;
@border-radius-large: 0px;
@border-radius-small: 0px;

@grid-float-breakpoint-max: 0px; // Don't collapse nav

// Don't import this here, use the Google Font Loader so we can get a callback once it's loaded
//@import "avenir-next.less";

// Reset and dependencies
@import "bootstrap/normalize.less";
@import "bootstrap/print.less";
@import "bootstrap/glyphicons.less";

// Font-Awesome
@import "font-awesome/variables.less";

@fa-font-path: "/fonts";

@import "font-awesome/mixins.less";
@import "font-awesome/path.less";
@import "font-awesome/core.less";
@import "font-awesome/larger.less";
@import "font-awesome/fixed-width.less";
@import "font-awesome/list.less";
@import "font-awesome/bordered-pulled.less";
@import "font-awesome/spinning.less";
@import "font-awesome/rotated-flipped.less";
@import "font-awesome/stacked.less";
@import "font-awesome/icons.less";

// Bootstrap core CSS
@import "bootstrap/scaffolding.less";
@import "bootstrap/type.less";
@import "bootstrap/code.less";
@import "bootstrap/grid.less";
@import "bootstrap/tables.less";
@import "bootstrap/forms.less";
@import "bootstrap/buttons.less";

// Bootstrap components
@import "bootstrap/component-animations.less";
@import "bootstrap/dropdowns.less";
@import "bootstrap/button-groups.less";
@import "bootstrap/input-groups.less";
@import "bootstrap/navs.less";
@import "bootstrap/navbar.less";
@import "bootstrap/breadcrumbs.less";
@import "bootstrap/pagination.less";
@import "bootstrap/pager.less";
@import "bootstrap/labels.less";
@import "bootstrap/badges.less";
@import "bootstrap/jumbotron.less";
@import "bootstrap/thumbnails.less";
@import "bootstrap/alerts.less";
@import "bootstrap/progress-bars.less";
@import "bootstrap/media.less";
@import "bootstrap/list-group.less";
@import "bootstrap/panels.less";
@import "bootstrap/responsive-embed.less";
@import "bootstrap/wells.less";
@import "bootstrap/close.less";

// Bootstrap components w/ JavaScript
@import "bootstrap/modals.less";
@import "bootstrap/tooltip.less";
@import "bootstrap/popovers.less";
@import "bootstrap/carousel.less";

// Bootstrap utility classes
@import "bootstrap/utilities.less";
@import "bootstrap/responsive-utilities.less";

// ID7 layout classes
@import "fixed-width-container.less";
@import "header.less";
@import "navigation";
@import "main-content";
@import "footer.less";
@import "utilities";

// Colour branding
@import "branding";

