.link-colour(@base) {
  a {
    color: @base;
    text-decoration: none;

    &:hover,
    &:focus {
      color: darken(@base, 7%);
      text-decoration: @link-hover-decoration;
    }
  }
}

@import 'mixins/flexbox.less';
@import 'mixins/css-triangles.less';
@import 'mixins/logo.less';
@import 'mixins/equal-height-columns.less';