.header-height(@width, @height, @bleed) {
  @height-total: @navbar-height + @height + @bleed;

  &,
  .carousel > .carousel-inner > .item {
    background-position: center bottom;
    background-size: @width @height-total;
  }

  min-height: @height-total;
  margin-bottom: -@bleed;

  &.opaque-navigation {
    background-position: center @id7-navigation-height;
  }
}

#header-image-container {
  .site-root & {
    &,
    .carousel > .carousel-inner > .item {
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-size: @container-sm (@id7-header-height-sm + @navbar-height);
    }

    // Set this on #header-image-container to allow the collapsing nav on smallscreens to fill this height up
    min-height: @id7-header-height-xs;

    @media (min-width: @screen-sm-min) {
      .header-height(@container-sm, @id7-header-height-sm, 0px);
    }

    @media (min-width: @screen-md-min) {
      .header-height(@container-md, @id7-header-height-md, 0px);
    }

    @media (min-width: @screen-lg-min) {
      .header-height(@container-lg, @id7-header-height-lg, 0px);
    }
  }

  .site-root.content-bleed & {
    @media (min-width: @screen-sm-min) {
      .header-height(@container-sm, @id7-header-height-sm, @id7-content-header-height);
    }

    @media (min-width: @screen-md-min) {
      .header-height(@container-md, @id7-header-height-md, @id7-content-header-height);
    }

    @media (min-width: @screen-lg-min) {
      .header-height(@container-lg, @id7-header-height-lg, @id7-content-header-height);
    }
  }

  position: relative;
  .strapline {
    display: none;
    position: relative;

    @media (min-width: @grid-float-breakpoint) {
      .site-root & {
        display: block;

        color: white;
        background: rgba(80, 80, 80, 0.3);

        h2 {
          margin: (@line-height-computed / 2) 0;
        }

        &.bottom {
          position: absolute;
          bottom: 0px;
        }
      }

      .site-root.content-bleed & {
        &.bottom {
          padding-bottom: @id7-content-header-height;
        }
      }
    }
  }

  .header-video {
    height: 100%;
    left: 0;
    overflow: hidden;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 0;

    iframe {
      height: 130%;
      margin: -5% -10%;
      width: 120%;
    }
  }
}